import React, { Component } from 'react';
import moment from 'moment';
import { requestHeaderDELETEWithAuth, requestHeaderGETWithAuth, requestHeaderPOSTWithAuth } from 'src/Rest-API/Header';
import { getUserDataFromLocalStorage, logoutProcedureForLocalStorage } from 'src/Utils/AuthRelatedThings';
import { Logger, getSearchResult, openPageInNewTab, reloadPage, goHome } from '../../Utils/Utils';
import { Loading } from '../Statuses/Statuses';
import StatusModal from '../Modals/StatusModal';
import FailureModal from '../Modals/FailureModal';
import { brandSet } from '@coreui/icons';
import {
    CButton,
    CCard,
    CCardBody,
    CCardGroup,
    CCol,
    CContainer,
    CForm,
    CInput,
    CInputGroup,
    CInputGroupPrepend,
    CInputGroupText,
    CRow,
    CTextarea,
    CInputCheckbox,
    CInputRadio
  } from '@coreui/react';
import CIcon from '@coreui/icons-react';
import MaterialReactTable from 'material-react-table';
import Select from 'react-select';
import { unitOfMeasurementsList, categoryTypesList, commodityTypesList, yesAndNoList } from 'src/Utils/Enums';
import { BACKEND_PRICE_TRACKER_SERVICE_BASE_URL } from 'src/Rest-API/EnvironmentSpecificThings';

//default
const FILE_NAME = "Category.js";

//default
const initialState = {
    isLoaded: false,
    loadingModalVisible: false,
    loadingModalState: 0,
    failureModalVisible: false,
    categoryListData: [],
    newCategoryData: {
        unitOfMeasurement: null,
        categoryType: null,
        commodityType: null,
        yesAndNo: null
    }
};


const tableColumnData = [
    {
        accessorKey: 'id',
        header: 'ID',
        enableEditing: false,
    },
    {
        accessorKey: 'name',
        header: 'Name',
        enableEditing: true,
    },
    {
        accessorKey: 'unitOfMeasurement',
        header: 'Unit Of Measurement',
        enableEditing: true,
    },
    {
        accessorKey: 'metadata.type',
        header: 'Category Type',
        enableEditing: true,
    },
    {
        accessorKey: 'metadata.commodityType',
        header: 'Commodity Type',
        enableEditing: true,
    },
    {
        accessorKey: 'metadata.eligibleForNoBrand',
        header: 'Eligible For NoBrand ?',
        enableEditing: false,
        Cell: ({ renderedCellValue, row }) => (renderedCellValue ? "YES" : "NO")
    },
    
    {
        accessorKey: 'createdAt',
        header: 'Created At',
        enableEditing: false,
        Cell: ({ renderedCellValue, row }) => (moment(renderedCellValue).zone('IST').format('YYYY/MM/DD HH:mm:ss ddd'))
    },
    {
        accessorKey: 'updatedAt',
        header: 'Updated At',
        enableEditing: false,
        Cell: ({ renderedCellValue, row }) => (moment(renderedCellValue).zone('IST').format('YYYY/MM/DD HH:mm:ss ddd'))
    },
];

class Category extends Component {

    //default
    constructor(props) {
        super(props);
        this.state = JSON.parse(JSON.stringify(initialState));
    }

    //default
    resetStateToDefault() {
        this.setState(JSON.parse(JSON.stringify(initialState)));
    }

    //default
    reloadData() {
        this.resetStateToDefault();
        this.fetchCategoryDataList();
    }

    //default
    componentDidMount() {
        document.title = "Expenses - Category";
        this.fetchCategoryDataList();
    }

    fetchCategoryDataList() {
        let API_URL = BACKEND_PRICE_TRACKER_SERVICE_BASE_URL + '/category/list?offset=0&from=admin-portal';
        const userData = getUserDataFromLocalStorage();

        fetch(API_URL, requestHeaderGETWithAuth(userData.authToken))
        .then(response => {
            if(response.status === 200)
                return response.json();
            if(response.status === 401) {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
                setTimeout(() => {
                    logoutProcedureForLocalStorage();
                    reloadPage();
                }, 500);
            } else {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
            throw new Error("HTTP status is " + response.status);
        })
        .then(
            (result) => {
                Logger(FILE_NAME, "fetchCategoryDataList", "FETCH_1", "result", result);
                if(result.code === 1001) {
                    //success
                    this.setState({
                        isLoaded: true,
                        categoryListData: result.data
                    });
                } else {
                    //something horrible happened
                    this.setState({
                        loadingModalVisible: false,
                        loadingModalState: 0,
                        failureModalVisible: true
                    });
                }
            },
            (error) => {
                Logger(FILE_NAME, "fetchCategoryDataList", "FETCH_1", "error", error);
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
        );
    }
    
    submitCreateCategoryForm = (event) => {
        event.preventDefault();

        let reqData = {
            name: event.target.inputName.value
        };

        if(this.doesStateContainAllRequiredCategoryParams() === false) {
            alert("Please fill all unfilled items in the form");
            return;
        }

        reqData.unitOfMeasurement = this.state.newCategoryData.unitOfMeasurement;
        reqData.metadata = {
            type: this.state.newCategoryData.categoryType,
            commodityType: this.state.newCategoryData.commodityType,
            eligibleForNoBrand: this.state.newCategoryData.yesAndNo === 'YES' ? true : false,
        };

        this.setState({
            loadingModalVisible: true,
            loadingModalState: 0
        });

        this.callCreateCategoryApi(reqData);
    }

    doesStateContainAllRequiredCategoryParams() {
        return (this.state.newCategoryData.unitOfMeasurement !== null
            && this.state.newCategoryData.categoryType !== null
            && this.state.newCategoryData.commodityType !== null
            && this.state.newCategoryData.yesAndNo !== null);
    }

    callCreateCategoryApi(newCategory) {
        let API_URL = BACKEND_PRICE_TRACKER_SERVICE_BASE_URL + '/category/save?offset=0&from=admin-portal';
        const userData = getUserDataFromLocalStorage();
        fetch(API_URL, requestHeaderPOSTWithAuth(JSON.stringify(newCategory), userData.authToken))
        .then(response => {
            if(response.status === 200)
                return response.json();
            if(response.status === 401) {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
                setTimeout(() => {
                    logoutProcedureForLocalStorage();
                    reloadPage();
                }, 500);
            } else {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
            throw new Error("HTTP status is " + response.status);
        })
        .then(
            (result) => {
                Logger(FILE_NAME, "callCreateCategoryApi", "FETCH_1", "result", result);
                if(result.code === 1001) {
                    //success
                    this.setState({
                        loadingModalState: 1,
                    });
                    setTimeout(() => {this.reloadData();}, 200);
                } else {
                    //something horrible happened
                    this.setState({
                        loadingModalVisible: false,
                        loadingModalState: 0,
                        failureModalVisible: true
                    });
                }
            },
            (error) => {
                Logger(FILE_NAME, "callCreateCategoryApi", "FETCH_1", "error", error);
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
        );
    }

    //default
    render() {

        //default
        Logger(FILE_NAME, "render", "", "this.state", this.state);

        return (
            this.state.isLoaded ? 
            <div>
                <h1>CATEGORY &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                    <CButton color="success" className="px-4" onClick={() => this.reloadData()}>Reload Data</CButton>
                </h1>
                <br />

                {
                    this.state.isLoaded ?
                        <MaterialReactTable
                            initialState={{ density: 'compact' }}
                            columns={tableColumnData} 
                            data={this.state.categoryListData}
                            enablePagination = {true}

                            editingMode={"row"}
                            enableEditing
                            onEditingRowSave={(exitEditingMode) => this.handleSaveRowEdits(exitEditingMode)}
                        /> : <br/>
                }

                <br /><br />

                {
                    /* Create a new category */
                    <CContainer>
                    <CRow className="justify-content-center">
                    <CCol md="8">
                        <CCardGroup>
                        <CCard className="p-4">
                            <CCardBody>
                            <CForm onSubmit={this.submitCreateCategoryForm}>
                                <h1>Save a new category</h1>

                                <CInputGroup className="mb-3">
                                    <CInputGroupPrepend>
                                        <CInputGroupText>
                                        <CIcon name="cilPencil" />
                                        </CInputGroupText>
                                    </CInputGroupPrepend>
                                    <CInput
                                        placeholder="Category Name" 
                                        autoComplete="on"
                                        type="text"
                                        step="any"
                                        id="inputName"
                                        label="Category Name"
                                        required
                                    />
                                </CInputGroup>

                                <CIcon name="cilPencil" /> <b>Commodity Type :</b>
                                <br />
                                <Select 
                                    onChange={(choice) => {
                                        const newCategoryData = this.state.newCategoryData;
                                        newCategoryData.commodityType = choice.value;
                                        this.setState({
                                            newCategoryData
                                        });
                                    }}
                                    options={commodityTypesList}
                                />
                                <br />

                                <CIcon name="cilPencil" /> <b>Category Type :</b>
                                <br />
                                <Select 
                                    onChange={(choice) => {
                                        const newCategoryData = this.state.newCategoryData;
                                        newCategoryData.categoryType = choice.value;
                                        this.setState({
                                            newCategoryData
                                        });
                                    }}
                                    options={categoryTypesList}
                                />
                                <br />

                                <CIcon name="cilPencil" /> <b>Unit of measurement :</b>
                                <br />
                                <Select 
                                    onChange={(choice) => {
                                        const newCategoryData = this.state.newCategoryData;
                                        newCategoryData.unitOfMeasurement = choice.value;
                                        this.setState({
                                            newCategoryData
                                        });
                                    }}
                                    options={unitOfMeasurementsList}
                                />
                                <br />

                                <CIcon name="cilPencil" /> <b>Eligible For NoBrand ?</b>
                                <br />
                                <Select 
                                    onChange={(choice) => {
                                        const newCategoryData = this.state.newCategoryData;
                                        newCategoryData.yesAndNo = choice.value;
                                        this.setState({
                                            newCategoryData
                                        });
                                    }}
                                    options={yesAndNoList}
                                />
                                <br />

                                <CRow>
                                    <CCol xs="6">
                                        <CButton color="success" className="px-4" type='submit'>SAVE</CButton>
                                    </CCol>
                                    <CCol xs="6" className="text-right">
                                        <CButton color="danger" className="px-0" onClick={() => this.reloadData()}>&nbsp;Reset values&nbsp;</CButton>
                                    </CCol>
                                </CRow>
                            </CForm>
                            </CCardBody>
                        </CCard>
                        </CCardGroup>
                    </CCol>
                    </CRow>
                    </CContainer> 
                }

                {/* //default */}
                <StatusModal visible={this.state.loadingModalVisible} loadingModalState={this.state.loadingModalState} />
                <FailureModal visible={this.state.failureModalVisible} />
            </div>
            : <Loading />
        );
    }
}

export default Category;
